import { useEffect } from "react";
import classes from "./listBox.module.scss";
export interface listCard {
  label: string;
  iconClass?: string;
  linkUrl?: string;
}
export const ListBox: React.FC<{
  next?: () => void;
  items?: listCard[];
  title?: string;
}> = ({ next, items = [], title }) => {
  useEffect(() => {
    const timer = setTimeout(() => {
      next?.();
    }, 1000);
    return () => clearTimeout(timer);
  }, [next]);
  return (
    <div className={classes.listBox}>
      {title ? <div className={classes["listBox-header"]}>{title}</div> : null}
      <div className={classes["listBox-body"]}>
        {items.map(({ label, iconClass, linkUrl }) => {
          return (
            <div className={classes["listBox-item"]} key={label}>
              <div className={classes["listBox-item-label"]}>
                {iconClass && <i className={`fa ${iconClass}`} />}
                {linkUrl ? (
                  <a href={linkUrl} target="_blank" rel="noreferrer">
                    {label}
                  </a>
                ) : (
                  label
                )}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};
