import { useEffect } from "react";
import { ListBox, listCard } from "./listBox/listBox";

export const Skills: React.FC<{ next: () => void }> = ({ next }) => {
  useEffect(() => {
    const timer = setTimeout(() => {
      next?.();
    }, 1000);
    return () => clearTimeout(timer);
  }, [next]);
  const skillsList: listCard[] = [
    "JavaScript / Typescript",
    "Nodejs",
    "Angular",
    "React",
    "Redux / NgRx",
    "Express",
    "Java / SpringBoot",
  ].map((label) => ({
    label,
  }));
  const toolsList: listCard[] = [
    "Git",
    "Microservices",
    "Nginx",
    "Docker",
    "K8s",
    "NATS Streaming",
  ].map((label) => ({
    label,
  }));
  return (
    <div className="row justify-content-around">
      <div className="col-8 col-md-4 col-lg-3">
        <ListBox items={skillsList} title="Languages / Frameworks"></ListBox>
      </div>
      <div className="col-8 col-md-4 col-lg-3">
        <ListBox items={toolsList} title="Tools"></ListBox>
      </div>
      <div className="col-lg-3"></div>
    </div>
  );
};
