import { useEffect } from "react";
import "./blinker.scss";
export const Blinker: React.FC<{
  next: () => void;
  txt?: string;
  styles?: React.CSSProperties;
}> = ({ next, txt = "", styles }) => {
  useEffect(() => {
    const timer = setTimeout(() => {
      next();
    }, 1000);
    return () => clearTimeout(timer);
  }, [next]);
  return (
    <span className="blinker" style={styles}>
      {txt}
    </span>
  );
};
